import React, {Component} from 'react';

class PeekAttributes extends Component {
    state = {
        left: 0,
        top: 0
    }
    componentDidMount() {
        document.addEventListener('mousemove', (e) => {
            this.setState({left: e.pageX, top: e.pageY});
            console.log('ok')
        });
    }
    render() {
        return (
            <div style={{left: this.state.left, top: this.state.top}}>Attributes</div>
        )
    }
}

export default PeekAttributes;